registerLocale('zh-TW', zhTW); // 

// ---  ---
const GlobalDatePickerStyle = createGlobalStyle`
  /*  */
  .react-datepicker-popper[data-placement^="bottom"] .react-datepicker__triangle {
    /*  */
    display: none;
  }

  .custom-calendar.react-datepicker {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; /*  */
    border: 1px solid #ddd; /*  */
    border-radius: 12px; /*  */
    box-shadow: 0 5px 15px rgba(0,0,0,0.1); /*  */
    padding: 5px; /*  */
    background-color: #fff; /*  */
  }

  /*  */
  .custom-calendar .react-datepicker__header {
    background-color: #f8f8f8; /*  */
    border-bottom: 1px solid #eee;
    border-top-left-radius: 10px; /*  */
    border-top-right-radius: 10px;
    padding-top: 10px;
  }

  /*  */
  .custom-calendar .react-datepicker__year-select,
  .custom-calendar .react-datepicker__month-select {
    padding: 5px 8px;
    border-radius: 15px; /*  */
    border: 1px solid #ccc;
    background-color: #fff;
    margin: 0 3px;
    font-size: 0.9em;
    cursor: pointer;
    transition: border-color 0.2s;

    &:focus {
      outline: none;
      border-color: #4a90e2; /* Focus  */
    }
  }

  /*  (/) */
  .custom-calendar .react-datepicker__navigation {
    top: 12px; /*  */
  }
  .custom-calendar .react-datepicker__navigation--previous {
     border-right-color: #aaa; /*  */
     left: 12px; /*  */
     &:hover { border-right-color: #333; }
  }
  .custom-calendar .react-datepicker__navigation--next {
     border-left-color: #aaa; /*  */
     right: 12px; /*  */
     &:hover { border-left-color: #333; }
  }

  /*  */
  .custom-calendar .react-datepicker__day-name {
    color: #666; /*  */
    font-weight: bold;
    font-size: 0.8em;
    width: 2.2rem; /*  */
    line-height: 2.2rem;
    margin: 0.1rem; /*  */
  }

  /*  */
  .custom-calendar .react-datepicker__day {
    color: #333;
    width: 2.2rem;
    line-height: 2.2rem;
    margin: 0.1rem;
    border-radius: 50%; /*  */
    transition: background-color 0.2s, color 0.2s;

    &:hover {
      background-color: #f0f0f0; /*  hover  */
      border-radius: 50%; /*  */
    }
  }

  /*  */
  .custom-calendar .react-datepicker__day--outside-month {
    color: #ccc; /*  */
  }

  /*  */
  .custom-calendar .react-datepicker__day--today {
    font-weight: bold;
    border: 1px solid #4a90e2; /*  */
  }

  /*  */
  .custom-calendar .react-datepicker__day--selected {
    background-color: #4a90e2; /*  */
    color: white; /*  */
    border-radius: 50%;
    &:hover {
        background-color: #3a80d2; /* Hover  */
    }
  }

  /*  ( isClearable  true) */
  .react-datepicker__close-icon {
    padding-right: 10px; /*  */
    &::after {
      background-color: #aaa; /*  */
      font-size: 16px;
      height: 18px; /*  */
      width: 18px;
      line-height: 18px;
      padding: 0;
      border-radius: 50%;
      text-align: center;
      top: 50%;
      transform: translateY(-50%);
    }
     &:hover::after {
       background-color: #f00; /* Hover  */
     }
  }
`;
