const FilteredCount = styled.div`
  margin-bottom: 10px;
  font-size: 14px;
  color: #666;
`;

const InspirationTemplates = () => {
  const navigate = useNavigate();
  const { setCurrentDesign } = useDesign();
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 767);
  const [filteredTemplates, setFilteredTemplates] = useState(templates);
  const [activeColorFilter, setActiveColorFilter] = useState('');
  
  // 
  const [filters, setFilters] = useState({
    color: '',
    size: '',
    tag: '', // 
    price: ''
  });
  
  // 
  const filterOptions = {
    color: [...new Set(templates.map(t => t.color))],
    size: ['14cm', '14-15cm', '15cm'],
    tags: [...new Set(templates.flatMap(t => t.tags))],
    price: ['1000', '1000-1500', '1500']
  };
  
  // 
  const handleFilterChange = (filterType, value) => {
    setFilters({
      ...filters,
      [filterType]: value
    });
  };
  
  // 
  const handleTagFilter = (e) => {
    //  e.target.value 
    setFilters({
      ...filters,
      tag: e.target.value
    });
  };
  
  // 
  const clearAllFilters = () => {
    setFilters({
      color: '',
      size: '',
      tag: '',
      price: ''
    });
    
    localStorage.removeItem('crystal_color_filter');
    localStorage.removeItem('filter_timestamp');
    setActiveColorFilter('');
  };
  
  //  clearColorFilter 
  const clearColorFilter = () => {
    localStorage.removeItem('crystal_color_filter');
    localStorage.removeItem('filter_timestamp');
    setFilteredTemplates(templates);
    setActiveColorFilter('');
  };
  
  //  localStorage 
  useEffect(() => {
    const selectedColor = localStorage.getItem('crystal_color_filter');
    
    if (selectedColor) {
      setActiveColorFilter(selectedColor);
      setFilters(prev => ({
        ...prev,
        color: selectedColor + '' // ""
      }));
    }
  }, []);
  
  // 
  useEffect(() => {
    let results = [...templates];
    
    // 
    if (filters.color) {
      const colorToFilter = filters.color.toLowerCase();
      results = results.filter(template => {
        const templateColor = template.color.toLowerCase();
        return templateColor.includes(colorToFilter);
      });
    }
    
    // 
    if (filters.size) {
      switch(filters.size) {
        case '14cm':
          results = results.filter(t => t.size < 140);
          break;
        case '14-15cm':
          results = results.filter(t => t.size >= 140 && t.size <= 150);
          break;
        case '15cm':
          results = results.filter(t => t.size > 150);
          break;
        default:
          break;
      }
    }
    
    // 
    if (filters.tag) {
      results = results.filter(t => 
        t.tags.some(tag => tag.toLowerCase() === filters.tag.toLowerCase())
      );
    }
    
    // 
    if (filters.price) {
      switch(filters.price) {
        case '1000':
          results = results.filter(t => t.price < 1000);
          break;
        case '1000-1500':
          results = results.filter(t => t.price >= 1000 && t.price <= 1500);
          break;
        case '1500':
          results = results.filter(t => t.price > 1500);
          break;
        default:
          break;
      }
    }
    
    setFilteredTemplates(results);
  }, [filters]);
  
  // 
  useEffect(() => {
    const selectedColor = localStorage.getItem('crystal_color_filter');
    const timestamp = localStorage.getItem('filter_timestamp');
    
    if (selectedColor && !filters.color) {
      // 
      const filterColor = selectedColor.toLowerCase();
      
      const colorMap = {
        '': ['', '', '', ''],
        '': ['', '', '', ''],
        '': ['', '', '', ''],
        '': ['', '', '', '', '', ''],
        '': ['', '', '', '', '', '', '', ''],
        '': ['', '', '', ''],
        '': ['', '', '', ''],
        '': ['', '', '', '', '', '', ''],
        '': ['', '', '', '']
      };
      
      const filtered = templates.filter(template => {
        const templateColor = template.color.toLowerCase();
        
        if (colorMap[filterColor]) {
          return colorMap[filterColor].some(c => 
            templateColor.includes(c.toLowerCase())
          );
        }
        
        return templateColor.includes(filterColor);
      });
      
      if (filtered.length === 0) {
        const relatedTemplates = templates.filter(template => {
          const tColor = template.color.toLowerCase();
          
          if (filterColor === '' || filterColor === '' || filterColor === '') {
            return tColor.includes('') || tColor.includes('') || 
                   tColor.includes('') || tColor.includes('');
          }
          
          if (filterColor === '' || filterColor === '') {
            return tColor.includes('') || tColor.includes('') || 
                   tColor.includes('');
          }
          
          if (filterColor === '' || filterColor === '' || filterColor === '') {
            return tColor.includes('') || tColor.includes('') || 
                   tColor.includes('') || tColor.includes('');
          }
          
          return tColor.includes('');
        });
        
        if (relatedTemplates.length > 0) {
          setFilteredTemplates(relatedTemplates);
        } else {
          setFilteredTemplates(templates);
        }
      } else {
        setFilteredTemplates(filtered);
      }
    }
    
  }, []);
  
  //  SurpriseGenerator 
  const calculateBeadPositions = (crystals) => {
    if (!crystals || crystals.length === 0) return [];
    
    const positions = [];
    const totalBeads = crystals.length;
    // 
    const baseRadius = 60;
    
    // 
    for (let i = 0; i < totalBeads; i++) {
      const crystal = crystals[i];
      
      // 
      const sizeFactor = crystal.size / 8; // (8mm)
      let displaySize = 20 * sizeFactor; // 
      
      // 
      displaySize = Math.max(15, Math.min(displaySize, 25));
      
      //  - 
      const angle = (i / totalBeads) * 360;
      
      positions.push({
        ...crystal,
        angle,
        radius: baseRadius,
        displaySize
      });
    }
    
    return positions;
  };
  
  const handleTemplateClick = (template) => {
    // 
    const newDesign = {
      size: template.size || 160, // 16cm
      crystals: template.crystals.map(crystal => ({
        ...crystal,
        id: `${crystal.id}-${Date.now()}-${Math.random()}`
      }))
    };
    
    setCurrentDesign(newDesign);
    
    // 
    if (isMobile) {
      // 
      navigate('/', { state: { fromTemplate: true } });
      
      // 
      localStorage.setItem('fromTemplate', 'true');
      localStorage.setItem('template_selected_time', Date.now().toString());
      
      // 
      if (typeof window.setMobileCategory === 'function') {
        window.setMobileCategory('profile');
      }
      
      console.log('');
    } else {
      // 
      navigate('/');
      console.log('');
    }
  };
  
  //  isMobile 
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 767);
    };
    
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  
  return (
    <Container>
      <Title> - </Title>
      
      {/*  */}
      <FiltersContainer>
        <FilterGroup>
          <FilterLabel></FilterLabel>
          <FilterSelect 
            value={filters.color}
            onChange={(e) => handleFilterChange('color', e.target.value)}
          >
            <option value=""></option>
            {filterOptions.color.map(color => (
              <option key={color} value={color}>{color}</option>
            ))}
          </FilterSelect>
        </FilterGroup>
        
        <FilterGroup>
          <FilterLabel></FilterLabel>
          <FilterSelect 
            value={filters.size}
            onChange={(e) => handleFilterChange('size', e.target.value)}
          >
            <option value=""></option>
            {filterOptions.size.map(size => (
              <option key={size} value={size}>{size}</option>
            ))}
          </FilterSelect>
        </FilterGroup>
        
        <FilterGroup>
          <FilterLabel></FilterLabel>
          <FilterSelect 
            value={filters.tag}
            onChange={handleTagFilter}
          >
            <option value=""></option>
            {filterOptions.tags.map(tag => (
              <option key={tag} value={tag}>{tag}</option>
            ))}
          </FilterSelect>
        </FilterGroup>
        
        <FilterGroup>
          <FilterLabel></FilterLabel>
          <FilterSelect 
            value={filters.price}
            onChange={(e) => handleFilterChange('price', e.target.value)}
          >
            <option value=""></option>
            {filterOptions.price.map(price => (
              <option key={price} value={price}>{price}</option>
            ))}
          </FilterSelect>
        </FilterGroup>
        
        <FilterGroup>
          <ClearAllButton onClick={clearAllFilters}>
            
          </ClearAllButton>
        </FilterGroup>
      </FiltersContainer>
      
      {/*  */}
      <FilteredCount>
         {filteredTemplates.length} 
      </FilteredCount>
      
      {/*  */}
      {activeColorFilter && !filters.color && (
        <FilterIndicator>
          <span>: {activeColorFilter}</span>
          <ClearFilterButton onClick={clearColorFilter}>
            
          </ClearFilterButton>
        </FilterIndicator>
      )}
      
      <TemplatesGrid>
        {filteredTemplates.map(template => {
          // 
          const beadPositions = calculateBeadPositions(template.crystals);
          
          return (
            <TemplateCard key={template.id} onClick={() => handleTemplateClick(template)}>
              <PreviewContainer>
                <PriceTag>${template.price}</PriceTag>
                <CirclePreviewContainer>
                  {beadPositions.map((bead, index) => (
                    <CircularCrystalBead 
                      key={`${template.id}-${index}`}
                      size={bead.displaySize}
                      angle={bead.angle}
                      radius={bead.radius}
                      index={index}
                    >
                      <BeadImage
                        src={bead.image}
                        alt={bead.name || ` ${index + 1}`}
                        onError={(e) => {
                          e.target.src = 'default-crystal.png'; // 
                        }}
                      />
                      <BeadGloss />
                    </CircularCrystalBead>
                  ))}
                </CirclePreviewContainer>
              </PreviewContainer>
              <TemplateInfo>
                <TemplateName>{template.name}</TemplateName>
                <DetailsRow>
                  <span>: {template.color}</span>
                  <span>: {(template.size/10).toFixed(1)} cm</span>
                </DetailsRow>
                <TemplateDescription>{template.description}</TemplateDescription>
                <TagsContainer>
                  {template.tags.map(tag => (
                    <Tag key={tag}>{tag}</Tag>
                  ))}
                </TagsContainer>
              </TemplateInfo>
            </TemplateCard>
          );
        })}
        
        {filteredTemplates.length === 0 && (
          <EmptyMessage>
            
          </EmptyMessage>
        )}
      </TemplatesGrid>
    </Container>
  );
};

export default InspirationTemplates; 