import styled from 'styled-components';
import { useState, useEffect, useCallback } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
//  react-joyride 
// import Joyride, { STATUS, EVENTS, ACTIONS } from 'react-joyride';

// 
import numerologyIcon from '../assets/Logo/numerology.png';
import crystalBallIcon from '../assets/Logo/crystal-ball.png';
import solutionIcon from '../assets/Logo/solution (1).png';
import surpriseBoxIcon from '../assets/Logo/surprise-box.png';
import SurpriseGenerator from '../components/SurpriseGenerator';


// 
import NumerologyCalculator from '../components/NumerologyCalculator';

// 
import InspirationTemplates from '../components/InspirationTemplates';

const PageContainer = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const PageTitle = styled.h2`
  margin-bottom: 10px;
  color: #333;
  text-align: center;
`;

const PageSubtitle = styled.p`
  margin-bottom: 30px;
  color: #666;
  text-align: center;
  font-size: 14px;
`;

const OptionsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 100%;
  max-width: 500px;
`;

const OptionCard = styled.div`
  background: #fff;
  border-radius: 12px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
  
  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  }
`;

const IconContainer = styled.div`
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Icon = styled.img`
  width: 100%;
  height: 100%;
  object-fit: contain;
`;

// SVG  SVG 
const SvgIcon = styled.svg`
  width: 70%; /*  SVG  */
  height: 70%;
  fill: #666; /*  SVG  */
`;

const OptionTitle = styled.h3`
  font-size: 16px;
  color: #333;
  margin: 0;
  text-align: center;
`;

const BackButton = styled.button`
  padding: 8px 16px;
  background: #f0f0f0;
  border: none;
  border-radius: 4px;
  margin-bottom: 20px;
  cursor: pointer;
  font-size: 14px;
  
  &:hover {
    background: #e0e0e0;
  }
`;

const HelperPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [activeComponent, setActiveComponent] = useState(null);
  
  // : URL  > localStorage > null
  const [selectedOption, setSelectedOption] = useState(() => {
    const savedPage = localStorage.getItem('helper_page');
    if (savedPage) {
      // 
      localStorage.removeItem('helper_page');
      console.log(" localStorage :", savedPage);
      return savedPage;
    }
    
    console.log(" null");
    return null;
  });
  
  // 
  useEffect(() => {
    // 
    window.setSelectedOption = setSelectedOption;
    
    return () => {
      // 
      window.setSelectedOption = undefined;
    };
  }, []);
  
  // 
  const resetSelectedOption = useCallback(() => {
    setSelectedOption(null);
    //  localStorage 
    localStorage.removeItem('crystal_color_filter');
    localStorage.removeItem('filter_timestamp');
    localStorage.removeItem('redirect_to_helper');
    localStorage.removeItem('helper_page');
    //  tour 
    // localStorage.removeItem('joyride_tour_status'); //  localStorage  Joyride 
  }, []);
  
  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const page = queryParams.get('page');
    if (page) {
      //  'guide'  activeComponent
      if (page !== 'guide') {
        setActiveComponent(page);
      } else {
        //  URL  guide activeComponent
        queryParams.delete('page');
        navigate(`?${queryParams.toString()}`, { replace: true });
        setActiveComponent(null);
      }
    } else {
      setActiveComponent(null);
    }
  }, [location.search, navigate]);
  
  //  handleOptionClick:  'guide' 
  const handleOptionClick = useCallback((option) => {
    console.log(":", option);
    const searchParams = new URLSearchParams(location.search);
    //  page 
    searchParams.set('page', option);
    //  tour  ()
    searchParams.delete('tour');
    navigate(`?${searchParams.toString()}`, { replace: true });
  }, [navigate, location.search]);
  
  // handleBack:  tour 
  const handleBack = useCallback(() => {
    const searchParams = new URLSearchParams(location.search);
    searchParams.delete('page');
    // searchParams.delete('tour'); //  tour
    navigate(`?${searchParams.toString()}`, { replace: true });
  }, [navigate, location.search]);
  
  //  activeComponent  -  guide case
  const renderActiveComponent = () => {
    switch (activeComponent) {
      case 'numerology':
        return <NumerologyCalculator />;
      case 'guidance':
        return <div></div>;
      case 'inspiration':
        return <InspirationTemplates />;
      case 'surprise':
        return <SurpriseGenerator />;
      default:
        return null;
    }
  };
  
  return (
    <PageContainer>
      {!activeComponent ? (
        <>
          <PageTitle></PageTitle>
          <PageSubtitle></PageSubtitle>
        </>
      ) : (
         activeComponent !== null && <BackButton onClick={handleBack}></BackButton>
      )}

      {activeComponent ? renderActiveComponent() : (
        <OptionsGrid>
          <OptionCard onClick={() => handleOptionClick('numerology')}>
            <IconContainer>
              <Icon src={numerologyIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
          
          <OptionCard onClick={() => handleOptionClick('guidance')}>
            <IconContainer>
              <Icon src={crystalBallIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
          
          <OptionCard onClick={() => handleOptionClick('inspiration')}>
            <IconContainer>
              <Icon src={solutionIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
          
          <OptionCard onClick={() => handleOptionClick('surprise')}>
            <IconContainer>
              <Icon src={surpriseBoxIcon} alt="" />
            </IconContainer>
            <OptionTitle></OptionTitle>
          </OptionCard>
        </OptionsGrid>
      )}
    </PageContainer>
  );
};

export default HelperPage; 